.App-header {
	position: absolute;
	top: 0;
	width: 100%;
	height: 126px;
	z-index: 10;
}

.App-navbar {
	position: absolute;
	top: 0;
	bottom: -1px;
	left: 40px;
	right: 40px;
}

.App-navbar .App-navbar-phone {
	position: absolute;
	top: 50%;
	margin-top: -21px;
}

.App-navbar .App-navbar-phone:hover > .App-navbar-phone__number {
	margin-left: 40px;
}

.App-navbar .App-navbar-phone:hover > .App-navbar-phone__img {
	background-color: rgba(242, 245, 250, .3);
}

.App-navbar .App-navbar-phone .App-navbar-phone__img {
	display: inline-block;
	width: 42px;
	height: 42px;
	border: 1.5px solid #F2F5FA;
	border-radius: 100px;
	-webkit-transition: background-color 0.5s;
	-o-transition: background-color 0.5s;
	transition: background-color 0.5s;
}

.App-navbar .App-navbar-phone .App-navbar-phone__img img {
	position: absolute;
	left: 50%;
	margin-left: -6px;
	top: 50%;
	margin-top: -9px;
}

.App-navbar .App-navbar-phone .App-navbar-phone__number {
	position: absolute;
	display: inline-block;
	color: #94979E;
	font-size: 16px;
	top: 50%;
	margin-top: -8px;
	margin-left: 30px;
	-webkit-transition: margin 0.3s;
	-o-transition: margin 0.3s;
	transition: margin 0.3s;
}

.App-navbar .App-navbar-logo {
	position: absolute;
	left: 50%;
	margin-left: -33px;
	top: 50%;
	margin-top: -21px;
}

.App-navbar .App-navbar-inquiry {
	position: absolute;
	right: 72px;
	top: 50%;
	margin-top: -21px;
}


