.App-pages {
	position: absolute;
	top: 126px;
	bottom: 100px;
	left: 0;
	right: -18px;
  overflow-y: scroll;
  overflow-x: hidden;
  z-index: 10;
}
.App-page {
	position: relative;
	min-height: 100%;
	-webkit-transition: margin 0.3s;
	-o-transition: margin 0.3s;
	transition: margin 0.3s;
	display: grid;
	grid-template-rows: 1fr;
}

.App-page__content {
	margin-top: 60px;
	margin-bottom: 40px;
	margin-left: 40px;
	margin-right: 40px;
	z-index: 10;
}


