.App-page-one__content {
	display: grid;
	grid-template-columns: 2fr 1fr 3fr;
	grid-template-rows: 2fr 2fr 1fr auto;
	color: #616972;
}

.App-page-one__content .App-page-one__item:nth-child(1) span {
	color: #F2F5FA;
}

.App-page-one__content .App-page-one__item:nth-child(1) {
	grid-column: 1 / 2;
	grid-row: 1 / 3;
}

.App-page-one__content .App-page-one__item:nth-child(2) {
	grid-column: 1 / 2;
	grid-row: 3 / 4;
	font-size: 36px;
}

.App-page-one__content .App-page-one__item:nth-child(3) {
	display: inline-block;
	grid-column: 1 / 2;
	grid-row: 4 / 5;
}

.App-page-one__content .App-page-one__item:nth-child(4) {
	grid-column: 2 / 3;
	grid-row: 1 / 2;
	font-size: 26px;
}

.App-page-one__content .App-page-one__item:nth-child(5) {
	
	grid-column: 2 / 3;
	grid-row: 2 / 3;
}

.App-page-one__content .App-page-one__item:nth-child(5) .App-page-one__item__play-video {
	width: 100%;
	display: inline-block;
	cursor: pointer;
}

.App-page-one__content .App-page-one__item:nth-child(5) .App-page-one__item__img {
	position: relative;
	display: inline-block;
	width: 42px;
	height: 42px;
	border: 1.5px solid rgba(242, 245, 250, .3);
	border-radius: 100px;
	-webkit-transition: background 0.3s;
	-o-transition: background 0.3s;
	transition: background 0.3s;
}

.App-page-one__content .App-page-one__item:nth-child(5) .App-page-one__item__img img {
	position: absolute;
	top: 50%;
	margin-top: -5px;
	left: 50%;
	margin-left: -4px;

}

.App-page-one__content .App-page-one__item:nth-child(5) .App-page-one__item__text {
	position: absolute;
	display: inline-block;
	margin-left: 30px;
	margin-top: 17px;
	color: #F2F5FA;
	font-size: 16px;
	font-family: Athelas-Regular;
	-webkit-transition: margin 0.3s;
	-o-transition: margin 0.3s;
	transition: margin 0.3s;
}

.App-page-one__content .App-page-one__item:nth-child(5) .App-page-one__item__play-video:hover .App-page-one__item__img {
	background-color: rgba(242, 245, 250, .3);
}

.App-page-one__content .App-page-one__item:nth-child(5) .App-page-one__item__play-video:hover .App-page-one__item__text {
	margin-left: 40px;
}


