.App-footer {
	position: absolute;
	width: 100%;
	height: 100px;
	bottom: 0;
	background-color: #F2F5FA;
	z-index: 10;
}

.App-footer .App-footer__content {
	position: relative;
	display: inline-block;
	width: 50%;
	margin-left: 40px;
	margin-right: 40px;
	margin-top: 40px;
	-webkit-transition: margin 0.3s;
	-o-transition: margin 0.3s;
	transition: margin 0.3s;
}

.App-footer:hover .App-footer__content {
	margin-top: 35px;
}

.App-footer .App-footer__content .App-footer__content__text {
	display: inline-block;
	font-size: 16px;
	font-family: Helvetica-Light;
	color: #818992;
}

.App-footer .App-footer__content .App-footer__content__down-arrow {
	position: absolute;
	display: inline-block;
	right: 0;
}

.App-footer .App-footer__contacts {
	position: absolute;
	display: inline-block;
	right: 40px;
	top: 50px;
	margin-top: -12px;
}

.App-footer .App-footer__contacts .App-footer__contacts__facebook{
	margin-right: 12px;
	cursor: pointer;
}

.App-footer .App-footer__contacts .App-footer__contacts__twitter{
	cursor: pointer;
}


