.App-page-two {
	display: grid;
	grid-template-rows: 250px 1fr;
}

.App-page-two__top{
	display: grid;
	grid-template-columns: 1fr 2fr;
	padding-top: 60px;
	padding-left: 40px;
	padding-right: 40px;
	background: #F2F5FA;
}

.App-page-two__bottom {
	display: grid;
	background: #E7EBF1;
}

.App-page-two-top-tabs__button {
	display: inline-block;
	padding: 14px 32px;
	border-radius: 100px;
	font-size: 16px;
	color: #818992;
	font-family: Athelas-Regular;
	cursor: pointer;
}

.App-page-two-top-tabs {
	position: relative;
	display: block;
	z-index: 2;
}

.App-page-two-top-tabs-basic::before {
	position: absolute;
	left: -10px;
	height: 45px;
	width: 140px;
	border-radius: 100px;
	background-color: #E7EBF1;
	z-index: -1;
	-webkit-transition: all 0.3s;
	-o-transition: all 0.3s;
	transition: all 0.3s;
	content: "";
}

.App-page-two-top-tabs-basic::after {
	position: absolute;
	height: 10px;
	width: 10px;
	left: 10px;
	top: 17px;
	border-radius: 100px;
	background-color: red;
	z-index: -1;
	-webkit-transition: all 0.3s;
	-o-transition: all 0.3s;
	transition: all 0.3s;
	content: "";
}

.App-page-two-top-tabs-additional::before {
	position: absolute;
	left: 125px;
	height: 45px;
	width: 180px;
	border-radius: 100px;
	background-color: #E7EBF1;
	z-index: -1;
	-webkit-transition: all 0.3s;
	-o-transition: all 0.3s;
	transition: all 0.3s;
	content: "";
}

.App-page-two-top-tabs-additional::after {
	position: absolute;
	height: 10px;
	width: 10px;
	left: 143px;
	top: 17px;
	border-radius: 100px;
	background-color: red;
	z-index: -1;
	-webkit-transition: all 0.3s;
	-o-transition: all 0.3s;
	transition: all 0.3s;
	content: "";
}















.Tab-basic {
	display: grid;
	grid-template-rows: 1fr auto;
	height: 100%;
	padding-left: 40px;
	padding-right: 40px;
}

.Tab-basic-top {
	display: grid;
	grid-template-columns: 2fr 1fr 3fr auto;
	margin-top: 40px;
}

.Tab-basic-top__item:nth-child(1) {
	color: #2E363F;
}

.Tab-basic-top__item:nth-child(2) {
	color: #818992;
}

.Tab-basic-top__item:nth-child(3) {
	justify-self: center;
	margin-top: -40px;
	height: 100%;
	opacity: 0;
}

.Tab-basic-top__item:nth-child(4) div {
	display: inline-block;
	background-color: #D8DEE7;
	padding: 15px;
	cursor: pointer;
}
.Tab-basic-top-arrow-left {
	border-radius: 100px 0 0 100px;
}

.Tab-basic-top-arrow-right {
	border-radius: 0 100px 100px 0;
}


.Tab-basic-bottom {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
	margin-bottom: 40px;
}

.Tab-basic-bottom__item {
	-ms-flex-item-align: center;
	    align-self: center;
	color: #A9ADB6;
}

.Tab-basic-bottom__item span {
	color: #525761;
}

.Tab-basic-bottom__item:nth-child(5) {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	justify-self: end;
}

.Tab-basic-bottom__item:nth-child(5) div {
	-ms-flex-item-align: end;
	    align-self: end;
	background-color: #B5BAC1;
	height: 2px;
	width: 40px;
	margin-right: 8px;
}

.Tab-basic-bottom__item:nth-child(5) div:nth-child(1) {
	height: 6px;
	background-color: #2A323B;
}




.Tab-additional {
	padding-left: 40px;
	padding-right: 40px;
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
	align-self: center;


}

.Tab-additional div {
	padding-right: 60px;
}

.Tab-additional h6 {
	margin-top: 40px;
	margin-bottom: 50px;
	color: #2E363F;
}

.Tab-additional p {
	color: #818992;
}


