.App-page-three__content{
  display: grid;
  grid-template-rows: 2fr 3fr;
  color: #F2F5FA;
}

 .App-page-three__list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
}

.App-page-three__list__item {
  padding-right: 60px;
}

.App-page-three__list__item h3 {
  margin-bottom: 50px;
  color: #616972;
}

.App-page-three__list__item h6 {
  margin-bottom: 50px;
}

.App-page-three__list__item p {
  color: #616972;
}


