.App-page-number {
	position: fixed;
	top: 176px;
	right: 40px;
	height: 164px;
	width: 440px;
	font-family: Bodoni SvtyTwo-ITC-TT-Bold;
	-webkit-transition: right 0.3s;
	-o-transition: right 0.3s;
	transition: right 0.3s;
	z-index: 11;
}

.App-page-number .App-page-number__this {
	position: absolute;
	font-size: 188px;
	
}

.App-page-number .App-page-number__all, .App-page-number__slash {
	position: absolute;
	right: 0;
	font-size: 68px;
}

.App-page-number .App-page-number__all {
	bottom: 0;
}



.App-page-number-color-white  .App-page-number__this{
	color: #F2F5FA;
}

.App-page-number-color-white  .App-page-number__all, .App-page-number__slash{
	color: #F2F5FA;
	opacity: .29;
}


.App-page-number-color-black  .App-page-number__this{
	color: #2A323B;
}

.App-page-number-color-black  .App-page-number__all, .App-page-number__slash{
	color: #D9DDE3;
}


