.App h1 {
	font-size: 70px;
  font-family: Bodoni SvtyTwo-ITC-TT-Book;
}

.App h2 {
	font-size: 76px;
  font-family: Bodoni SvtyTwo-ITC-TT-Book;
}

.App h3 {
	font-size: 60px;
  font-family: Bodoni SvtyTwo-ITC-TT-Book;
}

.App h4 {
  font-size: 26px;
  font-family: Helvetica-Regular;
}

.App h6 {
	font-size: 20px;
  font-family: Helvetica-Regular;
}

.App p {
	font-size: 16px;
  font-family: Helvetica-Light;
	line-height: 1.4;
}

.App {
  position: relative;
  height: 100vh;
  overflow: hidden;
  background-color: #192e37;
  background-image: url('./app-bg.png');
  background-size: cover;
  background-position: center;
}

.App-player {
  position: absolute;
  display: none;
  background-color: #000;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 10;
}

.close {
  position: absolute;
  z-index: 20;
  top: 40px;
  right: 40px;
  height: 42px;
  width: 42px;
  text-indent: -9999px;
  font-size: 0;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  background-color: transparent;
  border: none;
  cursor: pointer;
  -webkit-transition: background 0.3s;
  -o-transition: background 0.3s;
  transition: background 0.3s;
}

.close:before, .close:after {
  position: absolute;
  left: 20px;
  content: ' ';
  height: 43px;
  width: 2px;
  background-color: #D8D8D8;
}
.close:before {
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}
.close:after {
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}


.App-player-blur {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: .77;
  background-color: #000000;
  z-index: 1;
}

.App-player .video-react-control-bar {
  display: none;
}

.App-preloader {
  position: absolute;
  left: 40px;
  right: 40px;
  top: 125px;
  height: 1px;
  background-color: #D8D8D8;
  z-index: 11;
}

.Button {
	padding: 14px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  font-family: Athelas-Regular;
  border-radius: 100px;
  cursor: pointer;
  -webkit-transition: background 0.3s, color 0.3s;
  -o-transition: background 0.3s, color 0.3s;
  transition: background 0.3s, color 0.3s;
}

.Button:focus {
  outline: none;
}


.ButtonWhite {
	background-color: #F2F5FA;
  border: 1.5px solid #F2F5FA;
  color: #2D353F;
  
}
.ButtonWhite:hover {
  background-color: transparent;
  color: #F2F5FA;
}


.ButtonWhiteNoBg {
	background-color: transparent;
  border: 1.5px solid rgba(242, 245, 250, .3);
  color: #D1D7DB;
}

.ButtonWhiteNoBg:hover {
	background-color: rgba(242, 245, 250, .3);
}

.ButtonBlack {
  border: 1.5px solid #2A323B;
  background-color: #2A323B;
  color: #F2F5FA;
  padding: 14px 45px;
}

.ButtonBlack:hover {
  background-color: transparent;
  color: #2A323B;
}


